<template>
    <div>
        <div class="d-block header-section">
            <div class="float-left"> <h1><b>Measurement Units</b></h1></div>
            <div class="float-right"> <button type="button" class="btn btn-link" v-on:click="loadProductsforSKUAdd()"><img src="@/assets/images/icons/add.png" alt=""> NEW UNIT</button></div>
        </div>
        <!-- Modal content Add-->
        <b-modal id="addMeasurement" hide-footer centered>
            <template v-slot:modal-title>
                Add Measurement Unit
            </template>
            <div class="d-block">
                <form class="needs-validation" @keyup.13="addSKU">
                    <div class="form-row">
                        <div class="form-check form-check-inline">
                            <label class="label-coilsize col-md-4">Description</label>
                            <div class="col-md-8">
                                <div class="form-group" :class="{ 'form-group--error': $v.skuDescription.$error }">
                                    <textarea class="form-control mb-2" rows="3" id="comment" placeholder="Description" v-model.trim="skuDescription"></textarea>
                                    <p v-if="addSKuErrors"> <span class="text-danger" v-if="!$v.skuDescription.required">Description is required</span></p>
                                </div>
                            </div>
                        </div>
                    </div>
                        <div class="form-row">
                            <div class="form-check form-check-inline">
                                <label class="label-coilsize col-md-4">Unit</label>
                                <div class="col-md-6">
                                    <div class="form-group" :class="{ 'form-group--error': $v.stockKeepingUnit.$error }">
                                        <input type="text" class="form-control" id="newUnit" placeholder="Unit" v-model="stockKeepingUnit" />
                                    </div>
                                    <p v-if="addSKuErrors">
                                        <span class="text-danger" v-if="!$v.stockKeepingUnit.required">Unit is required</span>
                                        <span class="text-danger" v-else-if="!$v.stockKeepingUnit.checkDuplicateUnit">Unit already exist</span>
                                    </p>
                                </div>
                            </div>
                        </div>
                        <h5 class="w-50">Product(s)</h5>
                        <div class="form-row" v-for="(product) in arrProductsListforSKUAdd" :key="product.productId">
                            <div class="col-md-4 mb-3">
                                <div class="form-check custom-control custom-switch">
                                    <input type="checkbox" class="custom-control-input" :id="'unitProdName_'+product.productId" name="example" v-model="product.isActive">
                                    <label class="custom-control-label" :for="'unitProdName_'+product.productId">{{product.name}}<span v-if="product.origin==1"> Domestic</span><span v-else> Imported</span></label>
                                </div>
                            </div>
                        </div>
                </form>
            </div>
            <div class="modal-footer">
                <button type="button" class="btn btn-outline-primary btn-lg" @click="$bvModal.hide('addMeasurement')">Cancel</button>
                <button type="button" class="btn btn-lg btn-primary" v-on:click="addSKU()">Submit</button>
            </div>
        </b-modal>
        <!-- Modal content End-->
        <!-- Modal content Edit-->
        <b-modal id="editMeasurement" hide-footer hide-header-close centered>
            <template v-slot:modal-title>
                Edit Measurement Unit
            </template>
            <div class="d-block">
                <form>
                    <div class="form-row">
                        <div class="form-check form-check-inline">
                            <label for="validationCustom01" class="label-coilsize col-md-4">Description</label>
                            <div class="col-md-8">
                                <textarea class="form-control mb-2" rows="3" id="comment" v-model.trim="skuDescription" disabled></textarea>
                            </div>
                        </div>
                    </div>
                    <div class="form-row">
                        <div class="form-check form-check-inline">
                            <label for="validationCustom01" class="label-coilsize col-md-4">Unit</label>
                            <div class="col-md-6">
                                <input type="text" class="form-control mb-2" v-model.trim="stockKeepingUnit" placeholder="unit" disabled>
                            </div>
                        </div>
                    </div>
                    <h5 class="w-50">Product(s)</h5>
                    <div class="form-row" v-for="(product) in arrProductsListforSKUEdit" :key="product.productId">
                        <div class="col-md-4 mb-3">
                            <div class="form-check custom-control custom-switch">
                                <input type="checkbox" class="custom-control-input" :id="'swtAddEditProdName_'+product.productId" name="example" v-model="product.isActive">
                                <label class="custom-control-label" :for="'swtAddEditProdName_'+product.productId">{{product.name}}<span v-if="product.origin==1"> Domestic</span><span v-else> Imported</span></label>
                            </div>
                        </div>
                    </div>
                </form>
            </div>
            <div class="modal-footer">
                <button type="button" class="btn btn-outline-primary btn-lg" @click="warningMsg()">Cancel</button>
                <button type="button" class="btn btn-lg btn-primary" @click="updateSKU();">Submit</button>
            </div>
        </b-modal>
        <!-- Modal content End-->
        <!-- Deactivate Model -->
        <b-modal id="deactiveItem" hide-footer hide-header centered>
            <template v-slot:modal-title>
                Add Measurement Units
            </template>
            <div class="d-block">
                <div class="modal-body mt-4 mb-3" align="center">
                    <div class="mt-4 mb-4">
                        <img v-if="activeDeactiveSKuStatus" src="@/assets/images/icons/tick.png">
                        <img v-else src="@/assets/images/icons/warning-message-icon.png">
                    </div>
                    <h3>Are you Sure</h3>
                    <p v-if="activeDeactiveSKuStatus" class="mb-5">You want to activate this data</p>
                    <p v-else class="mb-5">You want to deactivate this data</p>
                    <button type="button" class="btn btn-outline-primary btn-lg mr-3" @click="$bvModal.hide('deactiveItem')">Cancel</button>
                    <button type="button" class="btn btn-primary btn-lg" @click="activateDeactivateSKUStatus(); $bvModal.hide('deactiveItem')">Ok</button>
                </div>
            </div>
        </b-modal>
        <!-- Deactivate Model End-->
		<div class="table-responsive">
        <table id="tab" class="table table-sm coilmanagement ">
            <thead>
                <tr class="ro">
                    <th scope="col" class="description">DESCRIPTION</th>
                    <th scope="col" class="unit">UNIT</th>
                    <th scope="col" class="status">STATUS</th>
                    <th scope="col" class="actions wid-340">ACTIONS</th>
                </tr>
            </thead>
            <tbody>
                <tr class="ro" v-for="units in getAllSKUs" :key="units.id">
                    <td class="al">{{units.description}}</td>
                    <td class="al">{{units.unit}}</td>
                    <td class="cl" v-if="units.isActive">Active</td>
                    <td class="text-danger" v-else>Inactive</td>
                    <td class="ad">
                        <div class="info-coil-container">
                            <b-dropdown id="info-coil-dropdown" ref="dropdown">
                                <template v-slot:button-content>
                                    <button type="button" class="btn btn-info btn-lg info" @click="getSkuInfo(units.id)"><img src="@/assets/images/icons/info.png"> Info</button>
                                </template>
                                <div class="info-coil">
                                    <div class="right-arrow"></div>
                                    <div class="modal-content">
                                        <div class="modal-header">
                                            <h5 class="modal-title">Info</h5>
                                        </div>
                                        <CoilInfo :productInfoDomestic="objSkuInfoForDomestic" :productInfoImported="objSkuInfoForImported" />
                                    </div>
                                </div>
                            </b-dropdown>
                        </div>
                        <button type="button" class="btn btn-primary btn-lg editicon" v-on:click="loadProductsforSKUEdit(units.id,units.unit,units.description)"><img src="@/assets/images/icons/edit.png"> Edit</button>
                         <button type="button" class="btn btn-dark btn-lg deactivate" :class="{ btndeactivate : !units.isActive }" v-on:click="getactiveDeactivateSkuId(units.id, !(units.isActive));">
                            <span v-if="units.isActive"><img src="@/assets/images/icons/deactivate.png"> DEACTIVATE</span>
                            <span v-else><img src="@/assets/images/icons/deactivate.png"> ACTIVATE</span>
                        </button>
                    </td>
                </tr>
            </tbody>
        </table>
		</div>
    </div>
</template>

<script>
    import CoilInfo from "../popup/CoilInfo";
    import toast from '@/toast';
    import { required } from 'vuelidate/lib/validators';
    export default {
        data() {
            return {
                addSKuErrors: false,stockKeepingUnit: '',skuDescription: '',activateDeactivateSKUId: '', 
                activeDeactiveSKuStatus: true,objSkuInfoForDomestic: [],objSkuInfoForImported: [],
                arrProductsListforSKUAdd: [],arrProductsListforSKUEdit: [],ChangeTracker:false,skuEditCounter:0
            }
        },
        mounted() {
            this.$store.dispatch("getAllSKUsDtoAsync");
            this.$store.dispatch("getAllProductsDtoAsync");
        },
        computed: {
            getAllSKUs() {return this.$store.getters.sKUsDto;}
        },
         watch:{
            arrProductsListforSKUEdit:{
                deep:true,
                handler(){
                    this.skuEditCounter+=1;
                    if(this.skuEditCounter>1){
                    this.ChangeTracker=true;
                    }
                }
            }
        },
        validations: {
            skuDescription: { required },
            stockKeepingUnit: {
                required,
                checkDuplicateUnit(value) {
                    if (this.$store.state.sKUsDto.filter(element => element.unit.toUpperCase() === value.toUpperCase()).length === 0) return true;
                    return false;
                }
            }
        },
        components: {CoilInfo},
        methods: {
            warningMsg(){
                if(this.ChangeTracker){
                if(confirm("Values are modified and not saved!!, If you click 'OK' then values won't be saved"))
                    this.$root.$emit('bv::hide::modal', 'editMeasurement');
                }
                else
                   this.$root.$emit('bv::hide::modal', 'editMeasurement');
            },
            getactiveDeactivateSkuId(id, activeStatus) {
                this.activateDeactivateSKUId = id;
                this.activeDeactiveSKuStatus = activeStatus;
                this.$root.$emit('bv::show::modal', 'deactiveItem');
            },
            activateDeactivateSKUStatus() {
                this.$store.dispatch("DeactivateSKUDtoAsync", [this.activateDeactivateSKUId, this.activeDeactiveSKuStatus]);
                this.$root.$emit('bv::hide::modal', 'deactiveItem');
            },
            getSkuInfo(unitID) {
                this.objSkuInfoForDomestic = []; this.objSkuInfoForImported = [];
                let prodSKUsById = this.$store.getters.sKUsDto.filter((element) => element.productSkuDtos.
                    some((prodSKU) => prodSKU.stockKeepingUnitId === unitID)).
                    map(element => { let sku = Object.assign([], element); return sku.productSkuDtos.filter(prodSKU => prodSKU.stockKeepingUnitId === unitID); });
                if (prodSKUsById.length !== 0) {
                    prodSKUsById[0].filter(element=>element.isActive==1).forEach(prodReel => {
                        var product = this.$store.getters.productsDto.filter(element => element.id === prodReel.productId);
                        if (product[0].length !== 0) {
                            if (product[0].origin === 1)
                                this.objSkuInfoForDomestic.push(product[0].name);
                            else
                                this.objSkuInfoForImported.push(product[0].name);
                        }
                    });
                }
            },
            loadProductsforSKUAdd() {
                this.stockKeepingUnit = ''; this.skuDescription = ''; this.arrProductsListforSKUAdd = [];this.addSKuErrors=false;
                this.$store.getters.productsDto.forEach(element => {
                    var objProdSKU = { id: 0, productId: element.id, name: element.name,origin:element.origin, stockKeepingUnitId: 0, unit: 0, description: '', isActive: false };
                    this.arrProductsListforSKUAdd.push(objProdSKU);
                });
                this.$root.$emit('bv::show::modal', 'addMeasurement');
            },
            loadProductsforSKUEdit(sKUid, skUunit, sKUdescription) {
                 this.ChangeTracker=false;this.skuEditCounter=0;this.stockKeepingUnit = skUunit; this.skuDescription = sKUdescription; this.arrProductsListforSKUEdit = [];
                this.$store.getters.productsDto.forEach(element => {
                    var objProdSKU = { id: 0, productId: element.id, name: element.name,origin:element.origin, stockKeepingUnitId: sKUid, unit: skUunit, description: sKUdescription, isActive: false };
                    this.arrProductsListforSKUEdit.push(objProdSKU);
                });
                let prodSKUsById = this.$store.getters.sKUsDto.filter((element) => element.productSkuDtos.
                    some((prodSKU) => prodSKU.stockKeepingUnitId === sKUid)).
                    map(element => { let sKU = Object.assign([], element); return sKU.productSkuDtos.filter(prodSKU => prodSKU.stockKeepingUnitId === sKUid); });
                if (prodSKUsById.length !== 0) {
                    this.arrProductsListforSKUEdit.forEach(sKU1 => {
                        prodSKUsById[0].forEach(sKU2 => {
                            if (sKU1.productId === sKU2.productId) {
                                sKU1.id = sKU2.id; sKU1.isActive = sKU2.isActive;
                            }
                        });
                    });
                }
                this.$root.$emit('bv::show::modal', 'editMeasurement');
            },
            async addSKU() {
                this.$v.$touch();
                this.addSKuErrors = this.$v.stockKeepingUnit.$anyError || this.$v.skuDescription.$anyError;
                if (!this.addSKuErrors) {
                    let objSKU = { id: 0, unit: this.stockKeepingUnit, description: this.skuDescription, isActive: true, productSkuDtos: [] };
                    this.arrProductsListforSKUAdd.forEach(element => {
                        if (element.isActive === true) { element.unit = this.stockKeepingUnit; element.description = this.skuDescription; delete element.name,element.origin; objSKU.productSkuDtos.push(element); }
                    });
                    this.$root.$emit('bv::hide::modal', 'addMeasurement');
                    await this.$store.dispatch("AddUpdateSKUDtoAsync", objSKU);
                    await toast.success("Measurement unit Added!..");
                }
            },
            async updateSKU() {
                let objSKU = { id: 0, unit: '', description: '', isActive: true, productSkuDtos: [] };
                this.arrProductsListforSKUEdit.forEach(element => {
                    if (!(element.id === 0 && element.isActive === false)) { objSKU.unit = element.unit; objSKU.description = element.description; objSKU.id = element.stockKeepingUnitId; delete element.name,element.origin; objSKU.productSkuDtos.push(element); }
                });
                this.$root.$emit('bv::hide::modal', 'editMeasurement');
                await this.$store.dispatch("AddUpdateSKUDtoAsync", objSKU);
                await toast.success("Measurement unit updated!..");
            }
        }		
    }
</script>
